
import Picker from 'vux/src/components/picker/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
Picker,
Popup,
XButton
},
props: ['isShow'],
data () {
return {
show: this.isShow,
data: [],
options: [
{
name: '区域1',
value: '1',
parent: 0
},
{
name: '区域2',
value: '2',
parent: 0
},
{
name: '视频1',
value: 's1',
parent: '1'
},
{
name: '视频2',
value: 's2',
parent: '1'
},
{
name: '视频1',
value: 's3',
parent: '2'
},
],
}
},
methods: {
change: function() {

},
close: function() {
this.$emit('close');
}
},
watch: {
isShow () {
this.show = this.isShow;
}
}
}



