
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
components: {
XHeader
},
props: ['title', 'isBack'],
data() {
return {
top_title: this.title,
back: this.isBack
}
},
methods: {
onLeft: function() {
this.$emit('left');
},
onRight: function() {
this.$emit('right');
}
},
}



